.card{
    width: 24rem;
    border: none;
    border-radius: 2%;
    box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
}
.card-body{
    background-color: #f9f8f6;
    border-radius: 2%;
    padding-top: .7rem;
}
.title-hlight{
    width: 100%;
    height: 8%;
    background-color: #7EC8E3;
    position: absolute;
}
.author-image{
    width: 100%;
    height: auto;
    border-radius: 50%;
}
.card-title{
    font-family: "Old Standard TT";
    font-style: italic;
    font-size: 1.5rem;
    margin-bottom: auto;
}
.story-author{
    font-family: "Old Standard TT";
    font-size: .8rem;
    margin-top: .2rem;
}
.card-text{
    font-family: 'Source Sans Pro';
    margin-top: 1rem;
    color: black;
    font-weight: 600;
}
.clearfix {
    overflow: auto;
  }

.myReact-icons{
    margin-left: auto;
}

